import React from 'react';

//Components
import RegisterForm from './RegisterForm';

interface ITypes {
  setFocus: (focusKey: string) => void;
}

function Register({ setFocus }: ITypes) {
  setFocus('SideBar');
  return (
    <div className="text-[white]">
      <RegisterForm />
    </div>
  );
}

export default Register;
